body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  height: 100%;
}

body {
  height: 100%;
  margin: 0;
  padding: 0;
  margin-top: 120px;
  background-color: #262626;
  font-size: 24px;
}

h1 {
  font-size: 2em;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.toggle-button {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 25px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}

.toggle-button:focus {
    outline: none;
}

.toggle-button__line {
    width: 30px;
    height: 2px;
    background: white;
}
.toolbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    border-bottom: 1px solid #222;
    box-shadow: 0 1px 0 #262626;
    background-color: #19191A;
    height: 90px;
}

.toolbar__logo {
    margin-left: 1rem;
}

.toolbar__logo img {
    width: 80px;
}

.toolbar__logo a {
    color: #fff;
}

.toolbar__navigation {
    display: flex;
    align-items: center;
    height: 100%;
    padding: 0 1rem;
}

.toolbar__navigation-items ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.toolbar__navigation-items li {
    padding: 0 .5rem;
}

.toolbar__navigation-items a {
    color: white;
    text-decoration: none;
}

.toolbar__navigation-items a:hover, .toolbar__navigation-items a:active {
    color: #fa923f;
}

.spacer {
    flex: 1 1;
}
h1 {
  text-align: center;
  color: #ffb604;
}

.lineup {
    display: flex;
    font-size: 20px;
    justify-content: center;
    list-style-type: none;
    color: #fff;
    background-color: #19191A;
    max-width: 500px;
  }

  .lineup-wrapper {
    max-width: 500px;
    margin: 0 auto;
    padding: 50px;
  }
  
  .lineup ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }

  .lineup ul li {
   padding: 5px;
   list-style-type: none;
   box-shadow: 0 1px 0 #222;
   border-bottom: 2px solid #222;
   background-color: #262626;
  }
  
  .player {
    width: 60%;
  }

  .position {
    width: 40%;
  }
 
.roster {
    display: flex;
    color: #fff;
    justify-content: center;
    list-style-type: none;
    flex-direction: column;
  }

  .roster-wrapper {
    max-width: 500px;
    margin: 0 auto;
    padding: 50px;
  }
  
  .roster a {
      text-decoration: none;
      color: #fff;
  }

  .roster ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  .roster ul:before {
      color: red !important;
  }
  .roster ul li {
   padding: 5px;
   list-style-type: none;
  }

  .player-card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .player-card h1 {
      margin-bottom: 10px;
      font-size: 20px;
      color: #fff;
  }

  .player-card .number {
      color: #ffb604;
      font-size: 50px;
      font-weight: 800;
  }

  .player-card h2 {
    margin-top: 5px;
    font-size: 16px;
    color: #fff;
  }

  .btn {
      text-decoration: none;
      font-size: 14px;
      font-weight: 600;
      margin-top: 25px;
      color:#ffb604;
  }
.schedule {
    display: flex;
    color: #fff;
    font-size: 16px;
    justify-content: center;
    list-style-type: none;
  }
  
  .schedule a {
      text-decoration: none;
      color: #fff;
  }

  .schedule ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  .schedule ul:before {
      color: red !important;
  }
  .schedule ul li {
   padding: 5px;
   list-style-type: none;
  }
.hero {
    height: 100vh;
}
.hero-content {
        display: flex;
        align-items: center;
        justify-content: center;
        color: #fff;
}

.hero-content img {
    max-width: 100%;
    width: 200px;
}
.side-drawer {
    height: 100%;
    background:#0d0787;
    box-shadow: 1px 0px 7px rgba(0,0,0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 70%;
    max-width: 300px;
    z-index: 200;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    -webkit-transition: -webkit-transform 0.3s ease-out;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.side-drawer.open {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.side-drawer ul {
    height: 100%;;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.side-drawer li {
    margin: 0.5rem 0;
}

.side-drawer a {
    font-size: 1.2rem;
    color: #fff;
    text-decoration: none;
}

.side-drawer a:hover,
.side-drawer a:active {
    color: #ffb604;
}
.backdrop {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0,0,0,0.3);
    z-index: 100;
}
